








































































.peo {
  position: absolute;
  top: 2px;
  bottom: 1px;
  right: 1px;
  left: 2px;
  font-size: 60px;
  line-height: 50px;
  background: #0460BC;

  .lines {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    text-align: left;
    line-height: 12px;

    .item {
      position: relative;
    }
  }

  .line {
    text-align: center;
    display: block;
    background: #545454;
    height: 2px;
    border-bottom: 1px solid #000;

    i {
      display: block;
      height: 100%;
      background: #07C160;
      transition: 1s;
    }
  }

  .dur i {
    background: #b9b9b9;
  }

  .name {
    font-size: 12px;
    position: absolute;
    left: 3px;
    bottom: 3px;
    line-height: 14px;
    text-shadow: 1px 1px 2px #000;
  }

  .iconfont {
    position: absolute;
    top: 6px;
    left: 0.25em;
    font-size: 0.7em;
    opacity: 0.5;
  }

  .leftHand {
    opacity: 1;
    text-shadow: 1px 1px 2px #000;
  }

  .rightHand {
    font-size: .4em;
    left: auto;
    top: auto;
    right: -5px;
    bottom: -2px;
    color: #fff;
    opacity: 1;
    text-shadow: 1px 1px 2px #000;
  }
  
  .stateDes{
    font-size: .4em;
    position: absolute;
    left: 0;
    right: 0;
    top:0;
    text-align: center;
    opacity: 0;
    z-index: 99;
    text-shadow: 1px 1px 2px #000;
  }
  
  .mask{
    display: none;
    position: absolute;
    left:0;
    top:0;
    right:-4px;
    bottom:-4px;
    background: rgba(0,0,0,.3);
  }
}

