






























































































































































.market {
  width: 100%;
  height: 100%;
  vertical-align: top;
  color: #fff;
  font-size: 12px;

  .intro {
    height: 170px;
    border: 1px solid #777;
    text-align: left;
    line-height: 1.8;
    position: relative;

    .in {
      position: absolute;
      left: 15px;
      right: 15px;
      top: 10px;
      bottom: 10px;
      overflow-y: auto;
    }

    .van-tag {
      margin-right: 8px;
    }

    .attr {
      color: #aaa;
      display: flex;
      flex-wrap: wrap;

      li {
        width: 33.3%;
      }
    }

    p {
      margin: 0;
    }
  }
}

.items {
  position: absolute;
  //margin: 0 -10px;
  overflow-x: hidden;
  overflow-y: auto;
  top: 182px;
  left: 0;
  right: 0px;
  bottom: 40px;
}

.goodsList .lines {
  display: none;
}

.btns{
  position: absolute;
  width: 100%;
  bottom:0;
  
}
