























































































































































































































































































































































































































































































.battle {
  height: 100%;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;

  .topBar {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    font-size: 12px;
    height: 18px;
    line-height: 18px;
    border-top: 4px #0077AA solid;

    .round {
      display: inline-block;
      background: #0077AA;
      padding: 0 10px;
      border-radius: 0 0 5px 5px;
    }

  }

  .topBar.enemyRound {
    border-top-color: #AA0000;

    .round {
      background: #AA0000;
    }
  }

  #mapIn {
    position: relative;
  }

  #map {
    display: block;
  }

  #mapMask {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
  }

  .elements {
    >li {
      position: absolute;

      .iconfont {
        font-size: 1em;
        color: #444;
      }
    }
  }

  .peos {
    >li {
      display: inline-block;
      position: absolute;
      opacity: 1;
      transform: rotate(0deg) scale(1, 1);

      .name {
        display: none;
      }
    }

    .peo {
      background: #0077AA;
    }

    .active {
      background: #f60;
    }

    .end {
      .peo .mask {
        display: block;
      }
    }

    .hit {
      font-size: .4em;
      position: absolute;
      left: 0;
      right: 0;
      top: 31%;
      //text-shadow: 1px 1px 0 #000;
    }
  }

  .enemys .peo {
    background: #aa0000;
  }

  .enemys .peoAP {
    //display: none;
  }

  .PeoStatus {
    position: absolute;
    top: 15px;
    right: 10px;
    left: 10px;

    .infoTop {
      padding-bottom: 5px;
    }
  }

  .skills {
    position: absolute;
    left: 0;
    bottom: 97px;
    right: 0;

    li {
      display: inline-block;
      position: relative;
      vertical-align: top;

      .in {
        position: absolute;
        left: 2px;
        right: 2px;
        top: 2px;
        bottom: 2px;
        background: #0077aa;
        line-height: 0.5em;
        border-radius: 5px;

      }

      .peoAP {
        bottom: 3px;
        opacity: .5;

        s {
          background: #fff;
        }
      }

      .name {
        font-size: 12px;
      }

      .disabled {
        background: #666 !important;
      }
    }

    .cur .in {
      background: #FF6600;
    }

  }

  .peoAP {
    position: absolute;
    height: 4px;
    bottom: -4px;
    left: 0;
    right: 0;
    font-size: 0;

    i {
      vertical-align: top;
      display: inline-block;
      background: #ccc;
      width: 4px;
      height: 4px;
      margin: 0 1px;
      border-radius: 10px;
      overflow: hidden;
    }

    s {
      display: block;
      width: 100%;
      height: 100%;
      background: #55A532;
    }
  }

  #animateMask {
    display: none;
    z-index: 99;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0);
  }
}
