

























































































































































































































.team {
  color: #fff;
  font-size: 12px;

  .peos,
  .formation {
    border-top: 1px solid #777;
    border-bottom: 1px solid #777;
  }

  .info {
    position: absolute;
    left: 10px;
    right: 10px;
    top: 38px;
    bottom: 150px;
    overflow-y: auto;
  }

  .equips {
    margin-bottom: 15px;

    ul {
      display: grid;
      grid-template-columns: repeat(5, 20%);
      grid-template-rows: 50px;
    }

    .in {
      margin: 0 auto;
      height: 100%;
      width: 60px;
      line-height: 50px;
      border: 1px solid #777777;
      vertical-align: middle;
      text-align: center;
      border-radius: 5px;
      color: #888;

      span {
        font-size: 16px;
      }

      .iconfont {
        font-size: 30px;
        color: #fff
      }
    }
  }

  .attrTable {
    width: 100%;
    //border-top: 1px solid #777;
    text-align: left;
    margin-bottom: 10px;

    .title td {
      padding: 5px 0;

      strong {
        color: #07C160;
        font-size: 14px;
        font-weight: normal;
      }
    }

    .label {
      width: 60px;
      text-align: justify;
      text-align-last: justify;
    }

    .van-rate {
      transform: scale(0.7);
    }

    .van-rate__icon {
      font-size: 8px;
    }
  }

  .peos {
    position: absolute;
    left: 0;
    right: 0;
    max-height: 70px;
    padding: 5px 0;
    bottom: 60px;
    overflow-y: hidden;
    word-break: keep-all;
    white-space: nowrap;

    >ul>li {
      position: relative;
      display: inline-block;
      width: 60px;
      height: 60px;
      margin: 5px;
      background: #222;
      line-height: 60px;
      opacity: .8;
    }

    .active {
      opacity: 1;
      position: relative;
      top: -6px;
    }
  }
}
