








































































































































































.BattleEnd {
  .title {
    font-size: 30px;
    padding-top: 20px;
  }

  .page {
    position: absolute;
    top: 90px;
    bottom: 0;
    right: 10px;
    left: 10px;

    .in {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 100px;
      top: 0;
      overflow-x: hidden;
      overflow-y: auto;
    }

    .btns {
      text-align: left;
      position: absolute;
      bottom: 10px;
    }
  }

  .goodsList .price {
    display: none;
  }

  .goodsList .lines {
    display: block;
  }

  .pageExp .in>ul {
    margin-top:-10px;
    >li {
      border: 1px solid #444;
      border-radius: 5px;
      text-align: left;
      margin: 20px 0;
      padding:10px;
      font-size: 14px;
      position: relative;

      .pic {
        position: relative;
        width: 60px;
        height: 60px;
        float: left;
        margin-right: 15px;
      }

      .con {
        overflow: hidden;
      }
      
      .lv{
        float: right;
        line-height: 60px;
        font-size: 16px;
      }
      
      .lvUp{
        position: absolute;
        right: 0;
        top:-10px;
        font-size: 22px;
        color: #ff0;
      }
    }
  }
}
