






































































.PeoStatus {
  font-size: 12px;
  .infoTop {
    padding: 10px 0;
    text-align: left;
    line-height: 22px;

    .name {
      font-size: 16px;

      i {
        font-size: 14px;
        opacity: .7;
        margin-left: 10px;
        ;
      }
    }

    .lv {
      font-size: 12px;

      strong {
        font-size: 16px;
      }
    }
  }

  .lines {
    position: relative;
    text-align: left;
    line-height: 17px;
    margin-bottom: 5px;

    .item {
      position: relative;
    }
  }

  .line {
    text-align: center;
    display: block;
    position: absolute;
    left: 40px;
    right: 0;
    top: 1px;
    background: #545454;
    height: 14px;
    line-height: 14px;
    border-radius: 10px;
    overflow: hidden;

    i {
      position: absolute;
      left: 0;
      top: 0;
      display: block;
      height: 100%;
      background: #07d165;
      background-image: linear-gradient(to left, #07d165 , #037c3c);
    }

    b {
      font-weight: normal;
      position: relative;
      text-shadow: 1px 1px 0 #000;
    }
  }
  
  .buffs{
    text-align: right;
  }

  .dur {
    i {
      background: #cfcfcf;
      background-image: linear-gradient(to left, #cfcfcf , #6f6f6f);
    }
  }
}
