






































































































































































.SwitchEquip {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  background: #000;

  dl {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    margin: 0;
    bottom: 245px;
    overflow-y: auto;
    display: flex;
  }

  dd {
    margin: 0;
    flex-grow: 1;
    border-right: 2px solid #000;
    line-height: 2;
    width: 25%;

    &:first-child {
      width: 60px;
    }

    &:last-child {
      width: 60px;
      border-right: 0;
    }

    li {
      min-height: 24px;
      background: #131313;
    }

    li:nth-child(2n) {
      background: rgba(0, 0, 0, 0);
    }

    li.green {
      color: #55A532;
    }

    li.red {
      color: #ff5d5d;
    }

    .icon {
      width: 60px;
      height: 50px;
      line-height: 50px;
      margin: 6px auto 6px auto;
      vertical-align: middle;
      text-align: center;
      border-radius: 5px;
      color: #888;
      border: 1px solid rgba(0, 0, 0, 0);

      span {
        font-size: 16px;
      }

      .iconfont {
        font-size: 30px;
        color: #fff
      }
    }

    .icon1 {
      border: 1px solid #777777;
    }
  }

  .items {
    top: auto;
    bottom: 44px;
    max-height: 200px;
    padding: 10px;
    background: #000;
    border-top: 1px solid #777;
    border-bottom: 1px solid #777;
    
    .price{
      display: none;
    }
    .lines{
      display: block;
    }
  }

}
